<template>
  <div id="orderCheck">
    <div class="header">
      <div class="van-hairline--bottom van-nav-bar">
        <div class="van-nav-bar__left">
          <i class="van-icon van-icon-arrow van-nav-bar__arrow" style="color: #000000 !important;" @click="$router.back(-1)"></i>
        </div>
        <div class="van-ellipsis van-nav-bar__title">订单详情</div>
        <div class="van-nav-bar__right">
          <a @click="tel(orderInfo.shop.tel)">
            <img src="static/img/icon_kf_dfhxq@2x.png" width="20px" style="vertical-align: middle;"/>
          </a>
        </div>
      </div>
    </div>
    <div class="login_box">
      <div style="margin: 5px 0;padding: 10px" class="background_color_ffffff" @click="checkAddress">
        <div style="display: inline-block">
          <img src="static/img/icon_sjr_dfxxq@2x.png" width="16px" style="margin-right: 10px;"/>
        </div>
        <div class="font_12" style="display: inline-block">
          <span>{{address.username}}<span class="color_8b8b8b">&nbsp;&nbsp;&nbsp;&nbsp;{{address.telephone}}</span></span>
          <br>
          <span class="color_8b8b8b">{{address.area}}&nbsp;{{address.detailaddress}}</span>
        </div>
      </div>
      <div style="margin: 5px 0;" class="background_color_ffffff">
        <div class="background_color_ffffff" style="padding: 10px;">
          <div>
            <img :src="orderInfo.shop.shoplogo" style="vertical-align: bottom;height: 16px;"/>
            <span class="font_14">{{orderInfo.shop.shopname}}</span>
            <span class="float_right font_12 color_8b8b8b" style="margin-top: 1px">订单编号:{{orderInfo.orderid}}</span>
          </div>
          <van-card style="margin-top: 0">
            <div slot="thumb">
              <img :src="orderInfo.goods.goodslogo.split(',')[0]" v-lazy="orderInfo.goods.goodslogo.split(',')[0]" width="100%" style="display: block;height:63px" />
            </div>
            <div slot="tags" class="font_12 padding_bottom_5">
              {{orderInfo.goods.goodsname}}
            </div>
            <div slot="tags" class="font_12 color_8b8b8b padding_bottom_5" style="height: 42px;overflow: auto;">
              {{orderInfo.goods.introduce}}
            </div>
          </van-card>
          <!--<div class="font_12" style="padding: 5px 0">-->
            <!--<span>运费</span>-->
            <!--<span class="float_right">¥5.00</span>-->
          <!--</div>-->
          <div class="font_12" style="padding: 5px 0">
            <span>付款时间</span>
            <span class="float_right">{{orderInfo.paytime}}</span>
          </div>
          <div class="font_12 text_right" style="padding: 5px 0">
            共{{orderInfo.goods.num}}件商品,合计<span class="color_ff3312">¥{{orderInfo.totalmoney}}</span>
          </div>
          <div class="text_right" style="padding: 5px 0">
            <button class="font_12 color_40a3f3 comment" @click="orderSubmit">立即付款</button>
          </div>
        </div>
      </div>
    </div>
    <van-actionsheet
      v-model="show"
      :actions="actions"
      @select="onSelect"
    />
    <van-actionsheet
      v-model="show1"
      :actions="actions1"
      @select="onSelect1"
    />
  </div>
</template>
<script>
  export default {
    name: 'orderCheck',
    data() {
      return {
        show:false,
        show1:false,
        show2:false,
        actions1:[{name:'微信',id:'1'},{name:'支付宝',id:'2'}],
        address:[],
        actions:[],
        orderInfo:[],
        orderId : '',
        userId:localStorage.getItem('userId'),
      }
    },
    mounted() {
      this.orderId = this.$store.state.orderCheck.orderid;
      this.getOrderInfo();
      this.getAddress();
    },
    methods:{
      tel(telNum){
        dsBridge.call('dialingTel',telNum);
      },
      checkAddress(){
        // this.show=!this.show
        if(!this.show2){
          this.$toast({
            message: '请先添加地址!',
            position: "bottom"
          });
          this.$router.push('addAddress');
        }
      },
      orderSubmit(){
        if(this.show2){
          this.show1 = true;
        }else{
          this.$toast({
            message: '请先添加地址!',
            position: "bottom"
          });
          this.$router.push('addAddress');
        }
      },
      onSelect1(item){
        this.show1 = false;
        this.pay(item.id);
        // if(item.id=='1'){
        //   alert('微信付款');
        // }else{
        //   alert('支付宝付款');
        // }
      },
      pay(item){
        let paytype;
        if(item==1){
          paytype = 'weixin'
        }else{
          paytype = 'alipay'
        }
        let $this = this;
        let obj = {
          addressid:this.address.addressid,
          orderid:this.orderId,
          paytype:paytype
        };
        let qs = require('qs');
        $this.$ajax.post("order/payorder", qs.stringify(obj), {
          headers: {
            'Content-Type': 'application/x-www-form-urlencoded'
          }
        })
        .then(function(rpdata) {
          if(rpdata.data.meta.code=='200'){
            if(paytype=='alipay'){
              dsBridge.call('zfb_pay',rpdata.data.data);
              dsBridge.register('alipayMsg', function(res) {
                if(res=='1'){
                  $this.$toast({
                    message: '付款成功!',
                    position: "bottom"
                  });
                  $this.$router.back(-1);
                }
              })
            }else{
              dsBridge.call('wx_pay',rpdata.data.data);
              dsBridge.register('wx_msg', function(res) {
                if(res=='1'){
                  $this.$toast({
                    message: '付款成功!',
                    position: "bottom"
                  });
                  $this.$router.back(-1);
                }
              })
            }
          }
        })
        .catch(function(err) {})
      },
      payMsg(){
        let $this = this;
        $this.$ajax.post("order/getOrderinfo", qs.stringify({
          userid: $this.userId,
          orderid: $this.orderId
        }), {
          headers: {
            'Content-Type': 'application/x-www-form-urlencoded'
          }
        })
        .then(function(rpdata) {
          if(rpdata.data.meta.code=='200'){
            if(rpdata.data.data.status!='0'){
              $this.$toast({
                message: '付款成功!',
                position: "bottom"
              });
              $this.$router.back(-1);
            }else{
              $this.$toast({
                message: '付款失败!',
                position: "bottom"
              });
            }
          }
        })
        .catch(function(err) {})
      },
      getAddress(){
        let $this = this;
        let obj = {
          userid: this.userId,
        };
        let qs = require('qs');
        $this.$ajax.post("address/getaddressList", qs.stringify(obj), {
          headers: {
            'Content-Type': 'application/x-www-form-urlencoded'
          }
        })
        .then(function(rpdata) {
          if(rpdata.data.meta.code=='200'){
            if(rpdata.data.data.length>0){
              $this.show2 = true;
              for(let i = 0;i<rpdata.data.data.length;i++){
                rpdata.data.data[i].id = rpdata.data.data[i].username;
                rpdata.data.data[i].name = rpdata.data.data[i].area + rpdata.data.data[i].detailaddress;
              }
              $this.actions = rpdata.data.data;
              $this.address = rpdata.data.data[0];
            }
          }
        })
        .catch(function(err) {})
      },
      getOrderInfo(){
        let $this = this;
        let obj = {
          userid: $this.userId,
          orderid: $this.orderId
        };
        let qs = require('qs');
        $this.$ajax.post("order/getOrderinfo", qs.stringify(obj), {
          headers: {
            'Content-Type': 'application/x-www-form-urlencoded'
          }
        })
        .then(function(rpdata) {
          if(rpdata.data.meta.code=='200'){
            $this.orderInfo = rpdata.data.data;
          }
        })
        .catch(function(err) {})
      },
      onSelect(item) {
        this.show = false;
        this.address = item;
      },
    }
  }
</script>
<style scoped>
  .comment {
    border: 1px solid #40a3fa;
    background: none;
    padding: 5px 15px;
    border-radius: 5px;
  }
  .backMoney {
    border: 1px solid #8b8b8b;
    background: none;
    padding: 5px 15px;
    border-radius: 5px;
  }
  .login_box{
    margin-top: 54px;
  }
</style>
<style>
  #orderCheck .van-actionsheet__item {
    height: 44px;
    line-height: 44px;
  }
  #orderCheck .van-actionsheet__name {
    font-size: 12px;
  }
  #orderCheck .van-card__thumb {
    top: 0;
    left: 0px;
    width: 90px;
    height: 75px;
    position: absolute;
  }
  #orderCheck .van-card {
    color: #333;
    height: 75px;
    font-size: 16px;
    background: #fafafa;
    position: relative;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    padding: 5px 15px 5px 100px;
  }
</style>



